* {
  font-family: 'Gloria Hallelujah', cursive;
  color: white;
}

html, body, #root, .App {
  max-width: 100%;
  overflow-x: hidden;
  background-color:black;
}

a, a:visited {
  color: #FFFFFF;
	text-decoration: none;
  -webkit-transition: color .2s;
  -o-transition: color .2s;
  transition: color .2s;
}

a:hover {
  -webkit-transition: color .2s;
  -o-transition: color .2s;
  transition: color .2s;
  color: #c55;
}


.App {
  text-align: center;
}

.App-Diamond {
  height:600px;
  width:600px;
  position:fixed;
  top:0px;
  left:0px;
  background-image: url(./site-chained-diamond.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.App-Skateboard {
  z-index:1;
  height:1500px;
  width:500px;
  top:0px;
  right:0px;
  position:absolute;
  background-image: url(./site-hanging-skate.png);
  background-size: contain;
  background-repeat: no-repeat;
  margin-bottom:50px;
}

.App-BigBox {
  z-index:0;
  margin: auto;
  margin-top:150px;
  text-align: center;
  height:500px;
  display:block;
  width:500px;
  background-image: url(./site-bigbox_info.png);
  background-size: cover;
}

.App-Creature {
  position:absolute;
  bottom:-1250px;
  height:425px;
  width:425px;
  background-image: url(./site-creature.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.App-Project {
  margin: auto;
  text-align: center;
  height: 200px;
  display: block;
  width: 500px;
  background-image: url(./site-projectbox.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.App-Project-Inner-Title {
  text-align: left;
  position: relative;
  font-size: 22px;
  top: 38px;
  left: 14px;
  height: 57%;
  width: 480px;
}

.App-Project-Inner-Body {
  text-align: left;
  position: relative;
  top: -40px;
  left: 16px;
  height: 57%;
  font-size: 13px;
  width: 480px;
}

.App-Project:hover {
  width: 525px;
}

.App-Footer {
  height:10px;
  width:100%;
  display:block;
}




